'use client';
import React, { useState, useRef, useEffect } from 'react';
import { Arrow } from '../icon/arrow';
import toast from 'react-hot-toast';

interface Message {
  role: string;
  content: string;
}

interface ApiResponse {
  success: boolean;
  data?: {
    content: string;
  };
  message?: string;
}

interface PromptBoxProps {
  isLoading: boolean;
  setIsLoading: (isLoading: boolean) => void;
  prompts: Message[];
  setPrompts: (prompts: Message[]) => void;
}

const PromptBox = ({ isLoading, setIsLoading, prompts, setPrompts }: PromptBoxProps) => {
  const [input, setInput] = useState('');
  const containerRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    containerRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [prompts]);

  const sendMessages = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!input.trim() || isLoading) return;

    const userPrompt: Message = {
      role: 'user',
      content: input,
    };

    setPrompts((prev) => [...prev, userPrompt]);
    setInput('');
    setIsLoading(true);

    try {
      const response = await fetch('/api/ai', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ prompt: [...prompts, userPrompt] }),
      });

      const data: ApiResponse = await response.json();

      if (!data.success) {
        toast.error(data.message || 'Failed to get a response from the AI.');
        return;
      }

      if (!data.data?.content) {
        toast.error('No content in the AI response.');
        return;
      }

      setPrompts((prev) => [
        ...prev,
        { role: 'assistant', content: data.data.content },
      ]);
    } catch (error: any) {
      console.error('Error: ', error);
      toast.error(`Error: ${error.message}`);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div>
      <form
        onSubmit={sendMessages}
        className={`w-full ${false ? 'max-w-3xl' : 'max-w-2xl'} bg-white/10 backdrop-blur-md drop-shadow-lg border-white border-2 rounded-3xl mt-4 transition-all shadow-2xs p-4`}
      >
        <textarea
          className="outline-none overflow-hidden resize-none bg-transparent break-words w-full"
          rows={2}
          placeholder="Type here your fav lyrics"
          onChange={(e) => setInput(e.target.value)}
          value={input}
        />
        <div>
          <button
            className={`${
              prompts.length  > 1 ? 'bg-purple-800 text-white' : 'bg-purple-200 text-purple-800'
            } flex ml-[590px] justify-end place-content-end p-2 rounded-full`}
            type="submit"
            disabled={isLoading}
          >
            <Arrow />
          </button>
        </div>
      </form>
    </div>
  );
};

export default PromptBox;