"use client";
import Sidebar from "@/app/components/ui/Sidebar";
import { useState } from "react";
import Image from "next/image";
import { motion } from "framer-motion";
import PromptBox from "@/app/components/ui/prompt";

interface Message {
  role: "assistant" | "user";
  content: string;
}

export default function Dashboard() {
  const [prompts, setPrompts] = useState<Message[]>([]);
  const [isLoading, setIsLoading] = useState(false);

  return (
    <div className="flex w-full h-screen bg-gradient-to-r from-indigo-100 via-purple-100 to-pink-100">
      <div className="left-0 sticky mr-96">
        <Sidebar />
      </div>
      <div className="flex flex-col w-full justify-center">
        {prompts.length === 0 ? (
          <div className="justify-center mx-80">
            <motion.div
              initial={{ opacity: 0, scale: 0 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{
                duration: 0.4,
                scale: { type: "spring", visualDuration: 0.4, bounce: 0.5 },
              }}
              className="flex rounded-full"
            >
              <Image
                alt="pic"
                className="flex justify-center rounded-full outline-1 mr-72 outline-purple-300 border-2 border-purple-200 bg-purple-100 p-1"
                width={60}
                height={60}
                src={"/logo.png"}
              />
            </motion.div>
            <motion.p
              animate={{ scale: 1.2 }}
              className="flex justify-start mr-40 text-3xl text-purple-800 font-bold mt-1.5"
            >
              Asong
            </motion.p>
          </div>
        ) : (
          <div className="flex w-full justify-center mr-72">
            {prompts.map((prompt, index) => (
              <div
                key={index}
                className={`flex items-start gap-3 ${
                  prompt.role === "user" ? "justify-start" : "justify-end"
                }`}
              >
                {prompt.role === "assistant" && (
                  <div className="h-8 w-8 rounded-full bg-white"></div>
                )}
                <div
                  className={`px-4 py-2 rounded-2xl max-w-[80%] ${
                    prompt.role === "user" ? "bg-blue-700" : "bg-blue-200"
                  }`}
                >
                  <p className="text-sm">{prompt.content}</p>
                </div>
                {prompt.role === "user" && (
                  <div className="w-8 h-8 rounded-full bg-blue-800 flex items-center"></div>
                )}
              </div>
            ))}
          </div>
        )}
        <div className={`${prompts.length === 0 ? "mt-2" : "mt-72"}`}>
          <PromptBox
            setIsLoading={setIsLoading}
            setPrompts={setPrompts}
            prompts={prompts}
            isLoading={isLoading}
          />
        </div>
      </div>
    </div>
  );
}